// src/components/HeartComponent.js

import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import styled, { keyframes } from "styled-components";
import { doc, getDoc } from "firebase/firestore";
import { db } from "../module/firebase";

export const Flowing = () => {
  const [loveAmount, setLoveAmount] = useState(0);
  const [error, setError] = useState(null);
  const navigate = useNavigate();
  const targetAmount = 500000; //  

  useEffect(() => {
    const fetchData = async () => {
      try {
        const projectRef = doc(db, "projects", "projectData");
        const docSnap = await getDoc(projectRef);

        if (docSnap.exists()) {
          const fetchedData = docSnap.data();
          if (fetchedData.Love !== undefined) {
            setLoveAmount(fetchedData.Love);
          } else {
            console.log("No Love data found!");
          }
        } else {
          console.log("No such document!");
        }
      } catch (error) {
        console.error("Error fetching data: ", error);
        setError("  .");
      }
    };

    fetchData();
  }, []);

  if (error) {
    return <div>{error}</div>;
  }

  const fillPercentage = Math.min((loveAmount / targetAmount) * 100, 100);
  const adjustment = (100000 - Math.min(loveAmount, 100000)) / 10000; // loveAmount  0 10   
  const adjustedFillPercentage = Math.min(fillPercentage + adjustment, 100);
  const filledHeight = (118.4 * adjustedFillPercentage) / 100;

  const getImageContent = (imageNumber) => {
    switch (imageNumber) {
      case 1:
        return '" 000 "      .';
      case 2:
        return '"! ! !"  .';
      case 3:
        return '"5 13!   !"    .';
      case 4:
        return '"  "      ';
      case 5:
        return '"    "     ,    ';
      case 6:
        return '"   "       .';
      case 7:
        return '" "    .';
      case 8:
        return '" !!"      .';
      case 9:
        return "  !    !";
      case 10:
        return '"  "      .';
      default:
        return "    .";
    }
  };

  const getImageTitle = (imageNumber) => {
    switch (imageNumber) {
      case 1:
        return "";
      case 2:
        return " ";
      case 3:
        return "";
      case 4:
        return " ";
      case 5:
        return " ";
      case 6:
        return "";
      case 7:
        return "3 ";
      case 8:
        return "";
      case 9:
        return " ";
      case 10:
        return "  ";
      default:
        return "    .";
    }
  };

  const handleImageClick = (index) => {
    navigate(`/detail?image=${index + 1}`);
  };

  return (
    <div className="component-container" style={{ textAlign: "center" }}>
      <HeartContainer>
        <HeartSvg viewBox="-6 0 140 118.4">
          <defs>
            <clipPath id="heartClip">
              <path
                d="M94.4,0c-13.6,0-24.4,10.8-30.4,17.2C58,10.8,47.2,0,33.6,0C14.8,0,0,14.8,0,33.6
              c0,22.8,30.8,43.6,64,72.4C97.2,77.2,128,56.4,128,33.6C128,14.8,113.2,0,94.4,0z"
              />
            </clipPath>
          </defs>
          <rect
            x="0"
            y={`${118.4 - filledHeight}`}
            width="128"
            height={filledHeight}
            fill="#FF99CC"
            clipPath="url(#heartClip)"
          />
          <path
            d="M94.4,0c-13.6,0-24.4,10.8-30.4,17.2C58,10.8,47.2,0,33.6,0C14.8,0,0,14.8,0,33.6
            c0,22.8,30.8,43.6,64,72.4C97.2,77.2,128,56.4,128,33.6C128,14.8,113.2,0,94.4,0z"
            fill="none"
            stroke="#FF99CC"
            strokeWidth="2"
          />
        </HeartSvg>
      </HeartContainer>

      <ContentBox>
        <Amount>
          {loveAmount.toLocaleString()}
          <SmallText></SmallText>
          <br />
          <TargetAmount>{targetAmount.toLocaleString()} </TargetAmount>
        </Amount>
      </ContentBox>
      <FlowingListTitle>
          100%   
      </FlowingListTitle>

      <FlowingList>
        {Array.from({ length: 10 }).map((_, index) => (
          <FlowingBox key={index}>
            {/* <FlowingLabel> X   </FlowingLabel> */}
            <FlowingBoxTop>
              <Image
                src={`${process.env.PUBLIC_URL}/pro${index + 1}.png`}
                alt={`Programm ${index + 1}`}
                className="image-item"
                onClick={() => handleImageClick(index)}
                style={{ cursor: "pointer" }}
              />
              <FlowingDesc>{getImageContent(index + 1)}</FlowingDesc>
            </FlowingBoxTop>
            <FlowingBoxBottom>
              <FlowingTag>{getImageTitle(index + 1)}</FlowingTag>
            </FlowingBoxBottom>
          </FlowingBox>
        ))}
      </FlowingList>
    </div>
  );
};

const ContentBox = styled.div`
  display: inline-block;
  text-align: center;
  margin-bottom: 20px;
`;
const Amount = styled.p`
  font-size: 40px;
  color: #333333;
  margin: 0;
  line-height: 0.8;
`;

const TargetAmount = styled.span`
  font-size: 14px;
  color: #777777;
  text-align: left;
`;

const SmallText = styled.span`
  font-size: 14px; /* ''    */
`;

const FlowingListTitle = styled.div`
  display: flex;
  justify-content: left;
  align-items: center;
  padding: 2rem 0;
  font-size: 13px;
`;

const pump = keyframes`
  0%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
`;

const HeartContainer = styled.div`
  position: relative;
  width: 300px;
  height: 300px;
  margin: 0 auto;
  animation: ${pump} 2s infinite;
`;

const HeartSvg = styled.svg`
  width: 100%;
  height: 100%;
  text-align: center;
`;

const FlowingList = styled.div`
  display: flex;
  flex-direction: column;
  row-gap: 0.8rem;
`;

const FlowingBox = styled.div`
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  border: 0.1rem solid rgb(222, 226, 230);
  border-radius: 0.8rem;
  overflow: hidden;
`;

const FlowingLabel = styled.div`
  position: absolute;
  top: 1.6rem;
  right: 1.2rem;
  padding: 0.8rem 1.2rem;
  background-color: #a5e5ff;
  border-radius: 1.6rem;
  color: #fff;
  font-weight: 700;
  font-size: 1.8rem;
`;

const Image = styled.img`
  width: 100%;
  max-width: 100%;
  height: auto;
  object-fit: cover;
`;

const FlowingBoxTop = styled.div`
  border-bottom: 1px solid rgb(222, 226, 230);
  text-align: start;
`;

const FlowingDesc = styled.div`
  width: 100%;
  padding: 0.8rem;
  font-size: 1%.6;
  color: grey;
`;

const FlowingTag = styled.div`
  padding: 0.8rem;
  font-size: 1.4rem;
  color: black;
  text-align: left;
`;

const FlowingBoxBottom = styled.div`
  width: 100%;
  padding: 0.8rem;
  text-align: start;
`;
